.Contact {
  display: flex;
  flex-direction: column;
  margin-top: 30px; }

.Contact-block {
  display: block;
  position: relative;
  max-width: 420px;
  margin: 20px 0;
  padding: 6px;
  background: #EFEFEF;
  border-radius: 4px;
  text-align: left; }

.Contact-blockTitle {
  position: absolute;
  top: -25px;
  left: 10px;
  color: #34495e;
  font-family: 'Lato';
  font-size: 14px;
  font-weight: bold;
  letter-spacing: 0px;
  line-height: 18px;
  text-transform: uppercase; }

.Contact-input--large + .select2-container {
  width: 100% !important; }

.Contact-attendeeName {
  flex: 6; }

.Contact-attendeeAllergy {
  flex: 4; }
  .Contact-attendeeAllergy .select2-container--default .select2-selection--single {
    min-width: 160px;
    width: 160px; }

.Contact-attendeeRemove {
  position: absolute;
  top: 12px;
  right: -25px;
  color: rgba(231, 76, 60, 0.5); }
  .Contact-attendeeRemove:hover {
    cursor: pointer;
    color: #e74c3c; }

@media only screen and (min-width: 0) and (max-width: 600px) {
  .Contact {
    width: calc(100% - 40px);
    margin: 0 auto; }
  .Contact-attendee {
    flex-direction: column;
    padding-bottom: 10px;
    margin-bottom: 10px;
    border-bottom: 1px solid #CCC; }
  .Contact-attendeeName {
    margin-right: 0 !important; }
  .Contact-attendeeAllergy {
    margin-top: 5px;
    width: 100% !important; }
  .Contact-attendeeAllergy .select2-container--default .select2-selection--single,
  .Contact-attendeeAllergy .select2-container {
    width: 100% !important; } }

