.Button {
  display: inline-block;
  padding: 10px 19px;
  line-height: 19px;
  border-radius: 6px;
  border: none;
  font-family: 'Lato';
  font-size: 17px;
  font-weight: bold;
  outline: none; }

.Button--primary {
  background: #1abc9c;
  color: white; }
  .Button--primary:hover, .Button--primary:focus {
    cursor: pointer;
    background: #17a689; }

.Button--disabled {
  background: #bdc3c7;
  color: rgba(255, 255, 255, 0.75); }
  .Button--disabled:hover, .Button--disabled:focus {
    cursor: default; }

.Button--success {
  background: #2ecc71;
  color: white; }
  .Button--success:hover, .Button--success:focus {
    cursor: default; }

.Button--error {
  background: #E74C3C;
  color: white; }
  .Button--error:hover, .Button--error:focus {
    cursor: pointer;
    background: #e43725; }

.Button--withIcon {
  padding: 7px 12px;
  background: #415B76;
  color: white;
  font-size: 14px; }
  .Button--withIcon:hover, .Button--withIcon:focus {
    background: #2f4255; }

.Button--withIcon.has-errors {
  background: #E74C3C; }
  .Button--withIcon.has-errors:hover, .Button--withIcon.has-errors:focus {
    background: #d62c1a; }

