.Navigation-menu {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 37px;
  height: 37px;
  z-index: 11; }

.Navigation-menuButton {
  display: flex;
  width: 37px;
  height: 37px;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  outline: none;
  color: rgba(0, 0, 0, 0.5); }
  .Navigation-menuButton:hover {
    color: rgba(0, 0, 0, 0.8); }

.Navigation-menuDropdown {
  position: absolute;
  display: none;
  bottom: 37px;
  right: 0px;
  width: 115px;
  padding: 6px 15px;
  background: rgba(0, 0, 0, 0.6);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  text-align: left; }

.Navigation-menuDropdown.slide-0 {
  background: #799796; }

.Navigation-menuDropdown.slide-1 {
  background: #566772; }

.Navigation-menuDropdown.slide-2 {
  background: #5E8053; }

.Navigation-menuDropdown.slide-3 {
  background: #B68837; }

.Navigation-menuDropdown.slide-4 {
  background: #B2665E; }

.Navigation-menuDropdown.slide-5 {
  background: #4D8683; }

.Navigation-menuDropdown.is-visible {
  display: block; }

.Navigation-menuDropdownItem {
  display: block;
  padding: 3px 0;
  font-family: 'Lato';
  font-size: 14px;
  font-weight: 300;
  letter-spacing: 0px;
  line-height: 18px;
  color: rgba(255, 255, 255, 0.7);
  text-decoration: none;
  text-transform: capitalize; }
  .Navigation-menuDropdownItem:hover {
    color: rgba(255, 255, 255, 0.9);
    text-decoration: underline; }

.flickity-page-dots {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  left: 50%;
  bottom: 0;
  width: 100%;
  padding: 10px 0;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.2);
  z-index: 10; }

.dot {
  position: relative;
  width: 11px;
  height: 11px;
  margin: 0 3px;
  padding: 0;
  border: none;
  border-radius: 8px;
  background: rgba(0, 0, 0, 0.1);
  text-indent: 0;
  border: 1px solid rgba(0, 0, 0, 0.2);
  outline: none; }
  .dot:hover {
    background: white;
    cursor: pointer; }

.dot:hover,
.dot.is-selected {
  width: 15px;
  height: 15px;
  margin-top: 0px;
  margin: 0 1px;
  background: white;
  border-color: white;
  border: 1px solid rgba(0, 0, 0, 0.4); }
  .dot:hover:hover,
  .dot.is-selected:hover {
    cursor: normal; }

.Navigation-menu.is-light .Navigation-menuDropdown {
  background: rgba(255, 255, 255, 0.9);
  border-bottom: none;
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  border-top: 1px solid rgba(0, 0, 0, 0.1); }

.Navigation-menu.is-light .Navigation-menuDropdownItem {
  color: rgba(0, 0, 0, 0.6); }

.flickity-page-dots.is-light {
  background: rgba(255, 255, 255, 0.9);
  border-top: 1px solid rgba(0, 0, 0, 0.1); }

@media only screen and (min-width: 0) and (max-width: 450px) {
  .flickity-page-dots {
    align-items: center;
    justify-content: center;
    bottom: 0;
    width: 100%;
    padding: 10px 0; } }

@media only screen and (min-height: 320px) and (max-height: 480px) {
  .flickity-page-dots {
    align-items: center;
    justify-content: center;
    bottom: 0;
    width: 100%;
    padding: 10px 0; } }

