.TravelList-map {
  position: absolute;
  right: 0;
  top: 0;
  width: 50%;
  height: 900px;
  overflow: hidden;
  z-index: -1; }

.TravelList-map .leaflet-marker-pane,
.TravelList-map .leaflet-shadow-pane {
  display: none !important; }

.TravelList {
  width: 100%;
  max-width: 1020px;
  margin: 40px auto 0; }

.TravelList .l-box {
  margin: 10px; }

.TravelList-item {
  position: relative;
  background: white;
  border: 1px solid #DEDEDE;
  border-radius: 6px;
  overflow: hidden; }

.TravelList-itemImage {
  width: 100%;
  height: 187px;
  background: #D8D8D8;
  overflow: hidden; }

.TravelList-itemInfo {
  display: block;
  padding: 20px;
  text-align: left; }

.TravelList-itemFooter {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border-top: 1px solid #EEEEEE;
  text-align: left; }

.TravelList-itemFooter > * {
  padding: 14px 20px; }

.TravelList-itemCost {
  position: absolute;
  top: 14px;
  right: 14px;
  padding: 4px 10px;
  font-weight: bold !important;
  font-family: 'Lato';
  font-size: 14px;
  border: 2px solid white;
  border-radius: 20px;
  background: rgba(0, 0, 0, 0.3);
  z-index: 10; }

.TravelList-itemTitle {
  font-weight: bold;
  font-family: 'Lato';
  text-transform: uppercase;
  line-height: 22px; }

.TravelList-itemDesc {
  font-size: 14px;
  font-family: 'Lato';
  line-height: 22px;
  color: #666666; }

.TravelList-itemLink {
  flex: 1;
  border-left: 1px solid #EEEEEE; }

.TravelList-itemWikipedia {
  flex: 1;
  border-left: 1px solid #EEEEEE; }

.TravelList-itemLink,
.TravelList-itemWikipedia {
  color: #999; }
  .TravelList-itemLink:hover,
  .TravelList-itemWikipedia:hover {
    color: #676767; }

.TravelList-itemType {
  flex: 10;
  color: #999;
  text-transform: capitalize;
  font-size: 14px; }

.TravelList-sponsorList {
  display: flex;
  justify-content: center;
  align-items: center; }

.TravelList-disclaimer {
  max-width: 300px;
  margin: 30px auto; }

@media only screen and (min-width: 600px) and (max-width: 1000px) {
  .TravelList-map {
    width: 75%;
    opacity: 0.75; } }

@media only screen and (min-width: 320px) and (max-width: 600px) {
  .TravelList-map {
    width: 100%;
    opacity: 0.5; }
  .TravelList {
    width: calc(100% - 40px); } }

@media only screen and (min-width: 0) and (max-width: 320px) {
  .TravelList {
    width: 300px;
    margin: 0 auto; }
  .TravelList .l-box {
    margin: 10px; } }

